.todoItem-container{
  display: flex;
  align-items: center;
  max-width: 335px;
  margin: 0 20px;
  height: 55px;
  background-color: var(--task-color);
  border-radius: 15px;
  overflow: hidden;
}
.todoItem-container p{
  width: 100%;
  text-align: center;
  font-size: 1.4rem;
  color: black;
  font-weight: 500;
}
.todoItem-icon{
  color: rgba(0, 0, 0, 0.278);
  cursor: pointer;
  transition-duration: 300ms;
  font-size: 2rem;
}


.todoItem-delete{
  position: relative;
  bottom: 13px;
  left: 92%;
  font-size: 1.8rem;
}
.todoItem-delete:hover{
  color: rgb(158, 24, 24);
}
.todoItem-complete{
  position: relative;
right: 90%
}
.todoItem-complete:hover{
  color: rgb(59, 132, 23);
}
.todoItem-completed{
  background-color: #868686;
  transition-duration: 500ms;
}
.todoItem-completed .todoItem-complete{
  color: rgba(0, 0, 0, 0.695);
}
.todoItem-completed p{
  font-style: italic;
  color: rgb(0, 0, 0);
  text-decoration: line-through;
}
